import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { User } from "../models/user";
import { environment } from "../../environments/environment";

@Injectable()
export class UserService {
  private url = environment.apiAddress;
  headers: HttpHeaders;
  constructor(private httpClient: HttpClient) {
    this.headers = new HttpHeaders({ 'content-type': 'application/json' });
  }

  getUsers(): Observable<User[]> {
    return this.httpClient.get<User[]>(this.url + '/user');
  }
  getUser(id: number): Observable<User> {
    return this.httpClient.get<User>(this.url + '/user/' + id);
  }
  addUser(user: User): Observable<any> {
    return this.httpClient.post<any>(this.url + '/user', JSON.stringify(user), { headers: this.headers, observe: 'response' });
  }
  updateUser(user: User): Observable<any> {
    return this.httpClient.put<any>(this.url + '/user/' + user.UserId, JSON.stringify(user), { headers: this.headers, observe: 'response' });
  }
  deleteUser(id: number): Observable<any> {
    return this.httpClient.delete<any>(this.url + '/user/' + id, { observe: 'response' });
  }
}
